import React, { Component } from 'react'
import './css.css';
import utils from './../../library'
import blockies from './../../blockies'
import Web3 from 'web3'

class Create extends Component {

    constructor(props) {
        super(props)
        this.state = {available:0,selling:false,price:0,ObjectId:"",author:"",creator:"",date:"",image:"loading",image_data:"loading",file:"",description:"loading",name:"loading",extension:"0x6E78E5eB786858Dfe88CF8EA51e297AD21D8105c",tags:""}
       
    }
   
    async send(){
        let ext=this.state.extension
      await window.ethereum.enable().then(async function (accounts) {   
        let  direct_factory
        
        if(ext=="0xa3fF3FaFa598dFd6208d07006C9a5aD60DC7a24D"){
            direct_factory = utils.direct_factory() 
        }else{
            direct_factory = utils.load_factory(ext) 
        }
        await direct_factory.methods.send(getUrlVars()["cx"],document.getElementById("address_input").value).send({gas: 80000, from: accounts[0]})
    })

    }

    async componentDidMount(){
        let _ObjectId
        const uri = await utils.itemMeta(getAddress())    
        console.log(uri)   
        const itemMeta = await utils.itemMetaJSON(uri) 
        console.log(itemMeta)
        let tempI=itemMeta["image"]
        if(tempI==null)tempI="ipfs://ipfs/QmPuktdZA9rGVnxFDDeBojEeBjqyZJeQzkVRjGdFkZbDsc"
        const _image=utils.uri2url(tempI)
        //document.getElementById("pad").style.background="url("+_image+")"
        //document.getElementById("pad").style.opacity="0.3"
        if(itemMeta["image_data"]!=null)tempI=itemMeta["image_data"]
       
        const _image_data=utils.uri2url(tempI)
        let _file=""
        const _type=itemMeta["type"]
        //alert(itemMeta["file"])
        if(_type!="image")_file=utils.uri2url(itemMeta["file"])
        const _description=itemMeta["description"]
        const _info=itemMeta["info"]
        const _extension=itemMeta["extensionAddress"]
        const _tags=itemMeta["keywords"]
        const _author=itemMeta["author"]
        const _creator=itemMeta["originalCreator"]
        utils.loadShopColors(_creator)
        const _date=itemMeta["date"]
        const _name=itemMeta["name"].replace(" (CURRICULAR.ETH)", "");
        let blocky=blockies.create({seed: getUrlVars()["cx"],size: 8,scale: 3})
        document.getElementById("blockies_sublabel").append(blocky)
        let balance=0
        let _price=0
        let supply=0;
        await window.ethereum.enable().then(async function (accounts) { 
        let abiEncodedValue = web3.eth.abi.encodeParameter('address', getUrlVars()["cx"])
        _ObjectId = web3.eth.abi.decodeParameter('uint256', abiEncodedValue)
        const {  REACT_APP_tokenABI } = process.env;
        const { REACT_APP_CYPHERSHOP } = process.env;
        let token = new web3.eth.Contract(JSON.parse(REACT_APP_tokenABI), getUrlVars()["cx"]) 
        balance=await token.methods.balanceOf(REACT_APP_CYPHERSHOP).call()
        let hodl=await token.methods.balanceOf(accounts[0]).call()
        supply=await token.methods.totalSupply().call()

        let endtext="y"
        if((supply/1000000000000000000)>1)endtext="ies"
    
        document.getElementById("hodlamount_items").textContent="you own "+hodl/1000000000000000000+" of "+supply/1000000000000000000+" cop"+endtext
        
        const { REACT_APP_CYPHERSHOPABI } = process.env;
        token = new web3.eth.Contract(JSON.parse(REACT_APP_CYPHERSHOPABI), REACT_APP_CYPHERSHOP) 
        _price=await token.methods.prices(getUrlVars()["cx"]).call()
        })
        this.setState({available:balance/1000000000000000000,selling:(balance>0)?true:false,basicprice:_price,price:_price,ObjectId:_ObjectId,creator:_creator,type:_type,date:_date,tags:_tags,image:_image,image_data:_image_data,author:_author,file:_file,info:_info,description:_description,name:_name,extension:_extension})
    try{document.getElementById("quantity").value=1}catch(e){}
    }

    async handleQuantity(){
      
        this.setState({price:""+(document.getElementById("quantity").value*this.state.basicprice)})
    }

    async buy(){
        let price=this.state.price
        await window.ethereum.enable().then(async function (accounts) { 
           
    
            const { REACT_APP_CYPHERSHOP } = process.env;
            const { REACT_APP_CYPHERSHOPABI } = process.env;
            let shop = new web3.eth.Contract(JSON.parse(REACT_APP_CYPHERSHOPABI), REACT_APP_CYPHERSHOP) 
            await shop.methods.buy(getUrlVars()["cx"]).send({value: price,from: accounts[0] , gas: 1650000 })
           
            })
            this.reloader()
    }

    async reloader(){
        let balance 
        let _price
        await window.ethereum.enable().then(async function (accounts) { 
            const {  REACT_APP_tokenABI } = process.env;
            const { REACT_APP_CYPHERSHOP } = process.env;
            let token = new web3.eth.Contract(JSON.parse(REACT_APP_tokenABI), getUrlVars()["cx"]) 
            balance=await token.methods.balanceOf(REACT_APP_CYPHERSHOP).call()
            let hodl=await token.methods.balanceOf(accounts[0]).call()
            let supply=await token.methods.totalSupply().call()
            const { REACT_APP_CYPHERSHOPABI } = process.env;
            token = new web3.eth.Contract(JSON.parse(REACT_APP_CYPHERSHOPABI), REACT_APP_CYPHERSHOP) 
            _price=await token.methods.prices(getUrlVars()["cx"]).call()

           
           
            let endtext="y"
            if((supply/1000000000000000000)>1)endtext="ies"
            document.getElementById("hodlamount_items").textContent="you own "+hodl/1000000000000000000+" of "+supply/1000000000000000000+" cop"+endtext
            

            })
            this.setState({available:balance,selling:(balance>0)?true:false,price:_price})
 
    }

    render(){

   console.log(this.props.update) 
   let file_Q=this.state.file
   //file_Q="QmYmiQT2bfW3bRry5brdLkq3En8c19y9L2sMvwmFdjQdiF"
   file_Q=file_Q.substr(file_Q.indexOf("Qm"))
 
        let author=this.state.author
        let date=this.state.date
        //author=author.substring(0,22)+"..."
        let listtags
        if( this.state.tags)
        listtags = this.state.tags.split(",").map((d) => <div className="tag">{d}</div>);
        return ( 
        <div>
            <div id="create_flyer">
            <div id="blockies_sublabel"></div>
            <div id="author_sublabel" onClick={() => window.open("/cyphervault/?account="+this.state.creator)}>{author}</div>
            <div className="clear"></div>
            <img id="create_image"  src={this.state.image} onClick={() =>  window.open("https://ethitem.com/?interoperable="+getUrlVars()["cx"])}></img>
            <div id="HappyFarm_tags"><div id="tags_container">{listtags}</div></div>
            <div className="clear"></div>
            {
                (this.state.type=="sound")?
                <><audio controls><source src={this.state.file_data} type="audio/mpeg"></source></audio></>
                :
                <></>
            }
{(this.state.selling)?
<>
<div className="pricelabel">{web3.utils.fromWei(this.state.price, 'ether')} ETH</div>
    {(this.state.available>1)?
    <>
    <input type="number" id="quantity" name="quantity" onChange={() => this.handleQuantity()} min="1" max={this.state.available}></input>
    <div className="buybuttonmulti dark darkblue" onClick={() => this.buy()}>BUY</div>
    <div className="clear"/>
    </>
    :
    <div className="buybutton dark darkblue" onClick={() => this.buy()}>BUY</div>
    }
</>
:
<div></div>}
<div id="hodlamount_items" className="light lightblue text white"></div>
            <div id="date_sublabel" className="text">{date}</div>
            <div id="HappyFarm_sublabel">{this.state.info}</div>

            <div className="clear"></div>
            <div className="linkscontainer">
            <div className="links">
                <div className="itemslogo" src="https://raw.githubusercontent.com/b-u-i-d-l/ETHITEM/main/assets/img/logo.png" onClick={() =>  window.open("https://ethitem.com/")}></div>
            </div>
            <div className="links">
                <img className="itemslogo2" src="https://i.imgur.com/ufiJHlL.png" onClick={() =>  window.open("https://etherscan.com/address/"+getUrlVars()["cx"])}></img>
            </div>
            <div className="links">
                <img className="itemslogo2" src="https://raw.githubusercontent.com/b-u-i-d-l/ETHITEM/main/assets/img/logo.png" onClick={() =>  window.open("https://ethitem.com/?interoperable="+getUrlVars()["cx"])}></img>
            </div>
            <div className="links">
                <img className="itemslogo" src="https://i.imgur.com/OfUrA6W.png" onClick={() =>  window.open("https://opensea.io/assets/0x0b64ac4B89471B86b5Fb5C2Dd79f73F3f25D3030/"+this.state.ObjectId)}></img>
            </div>
           
            </div>
        </div>
        <div className="vspacer"></div>
        <div className="dapps">

            {
                (this.state.type=="video")?
                <video width="320" height="240" controls>
                <source src={this.state.file_data} type="video/mp4"></source>
                </video>
                :
                (this.state.type=="vox")?
                <iframe id="voxdisplay" width="400" height="400" src={"/three/index.html?vox="+file_Q}>
                </iframe>
               /* <iframe id="voxdisplay" width="400" height="400" src={"https://maxxflyer.github.io/cyphervoxrender/index.html?vox="+file_Q}>
                </iframe>
                */
                :
                <img id="create_image_data"  src={this.state.image_data} onClick={() =>  window.open("https://ethitem.com/?interoperable="+getUrlVars()["cx"])}></img>
                
            }
                    <div className="create_mainlabel text">{this.state.name}</div>
         <div id="info">
             {this.state.description}
             
        </div>  
        <div id="creator_sublabel" className="text" onClick={() => window.open("/cyphervault/?account="+this.state.creator)}>{this.state.creator}</div> 
        </div>
      <br></br>
        </div>
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

function getImage() {
    var t=getUrlVars()
    var tt=""
    if(t["curriculum"]=="entertainer")tt=""
    if(t["curriculum"]=="pr")tt=""
    if(t["curriculum"]=="townkey")tt=""
    if(t["curriculum"]=="medal")tt=""

    if(t["curriculum"]=="cup")tt=""
    if(t["curriculum"]=="clown")tt=""
    if(t["curriculum"]=="unicorn")tt=""
    if(t["curriculum"]=="shit")tt=""

    if(t["curriculum"]=="idea")tt=""
    if(t["curriculum"]=="whitehat")tt=""
    if(t["curriculum"]=="blackhat")tt=""
    if(t["curriculum"]=="help")tt=""
    return tt;
}

function getName() {
    return getUrlVars()["curriculum"];
}

function getAddress() {
    return getUrlVars()["cx"];
}
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Create